import React, { Component, Fragment, Suspense } from "react";
import "../Styles/MealViewer.css";
import { wait, ProgressBar } from "../dependencies";
import { S } from "selector_dom"
import { userData, data, storeData } from "../Data";
import { updateRenderState } from "..";
import { dateGetter, dateIndex } from "../NavicationBar/MainPage";

const ItemCard = React.lazy(() => import("../Foods/ItemCard"));
const CreateMeal = React.lazy(() => import("./CreateMeal"));

let tmpMealName;
let tmpKcal;
export default class MealViewer extends Component {
  componentDidMount() {
    this.props.doAnimation ? this.introPageTransition() : this.introPageFadeTransition();

    this.addProgressBars();

    if (this.props.name) {
      tmpMealName = this.props.name;
      tmpKcal = this.props.kcal;
    }
    document.querySelector("meta[name=theme-color]").setAttribute("content", JSON.parse(window.localStorage.getItem("theme"))[1]);
  }
  componentWillUnmount() {
    document.querySelector("meta[name=theme-color]").setAttribute("content", JSON.parse(window.localStorage.getItem("theme"))[0]);
  }
  getMealData = () => {
    return userData.favorite.meals
      .filter((e) => e.mealName === (this.props.name ? this.props.name : tmpMealName))[0]
      .content.map((e) => (e = Object.assign({}, data.filter((el) => el.id === e[0])[0], { value: e[1], input: e[2] })));
  };

  renderFoodsInTheMeal = () => {
    return this.getMealData().map((e) => (
      <Suspense fallback="" key={Math.random() * 100}>
        <ItemCard
          key={e.id + Math.random() * 1000}
          name={e.name}
          kcal={Math.round((e.kcal / 100) * (e.value * e.input))}
          serving={e.value === 1 ? e.input + " غرام " : e.input + "  " + e.serving.filter((el) => el[1] === e.value)[0][0]}
          id={e.id}
          brand={e.brand}
          description={e.description}
          value={e.value}
          input={e.input}
        />
      </Suspense>
    ));
  };

  sumNutrition = (n) =>
    this.getMealData()
      .map((e) => ((e.value * e.input) / 100) * e[n])
      .reduce((a, b) => a + b);

  fatPercent = () =>
    Math.min(
      Math.max(Math.round(((this.sumNutrition("fat") * 9) / (this.props.kcal ? this.props.kcal : tmpKcal)) * 100), 0),
      100
    );

  proteinPercent = () =>
    Math.min(
      Math.max(Math.round(((this.sumNutrition("protein") * 4) / (this.props.kcal ? this.props.kcal : tmpKcal)) * 100), 0),
      100
    );

  carbPercent = () =>
    Math.min(
      Math.max(Math.round(((this.sumNutrition("carb") * 4) / (this.props.kcal ? this.props.kcal : tmpKcal)) * 100), 0),
      100
    );

  CalcCarbPercent = () =>
    this.carbPercent() > 15 ? Math.min(Math.max(100 - (this.fatPercent() + this.proteinPercent()), 0), 100) : this.carbPercent();

  introPageTransition = async () => {
    const page = document.getElementById("mealViewerPage");
    const pageHeader = document.getElementById("mealViewerHeader");

    page.style.cssText = `position: fixed; bottom: 50%; left: 50%; opacity: 0; transform: translate(-50%, 100%); transition: bottom 200ms`;
    pageHeader.style.position = "relative";

    await wait(10);
    page.style.bottom = "100%";
    page.style.opacity = "1";

    await wait(200);
    page.removeAttribute("style");
    pageHeader.style.removeProperty("position");
  };

  introPageFadeTransition = async () => {
    const page = document.getElementById("mealViewerPage");
    page.style.opacity = "0";
    page.style.transition = "opacity 350ms";
    await wait(20);
    page.style.opacity = "1";
    await wait(350);
    page.removeAttribute("style");
  };

  okButtonHandle = () => {
    const mealData = userData.favorite.meals.filter((e) => e.mealName === (this.props.name ? this.props.name : tmpMealName))[0]
      .content;
    const mealName =
      window.location.hash === "#Meals#MealViewer" ? S("#mealViewerSelectMeal").val() : window.location.hash.split("#")[2];
    const getDataByDate = userData.diary.filter((e) => e.date === dateGetter(dateIndex))[0];
    if (getDataByDate) {
      getDataByDate[mealName] ? getDataByDate[mealName].push(...mealData) : (getDataByDate[mealName] = [...mealData]);
    } else {
      userData.diary.push({ date: dateGetter(dateIndex), [mealName]: [...mealData] });
    }

    storeData();
    window.history.back();
  };

  trashButtonHandle = () => {
    const getIndex = userData.favorite.meals.indexOf(
      userData.favorite.meals.filter((e) => e.mealName === (this.props.name ? this.props.name : tmpMealName))[0]
    );
    userData.favorite.meals.splice(getIndex, 1);

    storeData();
    window.history.back();
  };

  editHandle = () => {
    const getIndex = userData.favorite.meals.indexOf(
      userData.favorite.meals.filter((e) => e.mealName === (this.props.name ? this.props.name : tmpMealName))[0]
    );
    userData.favorite.tmpMeal.mealName = this.props.name ? this.props.name : tmpMealName;
    userData.favorite.tmpMeal.mealContent = [...userData.favorite.meals[getIndex].content];
    updateRenderState(
      <Suspense fallback="" key={Math.random() * 100}>
        <CreateMeal
          key={Math.random() * 100}
          doAnimation={true}
          editMode={true}
          oldName={this.props.name ? this.props.name : tmpMealName}
        />
      </Suspense>
    );
    window.history.pushState("", "", "/EditMeal");
  };

  backButtonHandle = () => window.history.back();

  addProgressBars = () => {
    ProgressBar("#mealNutritionInfoCrabsPercent", {
      input: this.CalcCarbPercent(),
      R: 40,
      width: 5,
      fillColor: "none",
      progressColor: "var(--text-kinda-black)",
      textColor: "var(--text-kinda-black)",
      backgroundColor: "var(--background-gray)",
      lineCap: "none",
    });
    ProgressBar("#mealNutritionInfoProteinPercent", {
      input: this.proteinPercent(),
      R: 40,
      width: 5,
      fillColor: "none",
      progressColor: "var(--text-kinda-black)",
      textColor: "var(--text-kinda-black)",
      backgroundColor: "var(--background-gray)",
      lineCap: "none",
    });
    ProgressBar("#mealNutritionInfoFatPercent", {
      input: this.fatPercent(),
      R: 40,
      width: 5,
      fillColor: "none",
      progressColor: "var(--text-kinda-black)",
      textColor: "var(--text-kinda-black)",
      backgroundColor: "var(--background-gray)",
      lineCap: "none",
    });
    const carbsTitle = document.createElement("p");
    const proteinTitle = document.createElement("p");
    const fatTitle = document.createElement("p");
    carbsTitle.classList.add("mealPercentTitles");
    proteinTitle.classList.add("mealPercentTitles");
    fatTitle.classList.add("mealPercentTitles");
    carbsTitle.innerHTML = "الكربوهيدرات";
    proteinTitle.innerHTML = "البروتين";
    fatTitle.innerHTML = "الدهون";
    document.getElementById("mealNutritionInfoCrabsPercent").append(carbsTitle);
    document.getElementById("mealNutritionInfoProteinPercent").append(proteinTitle);
    document.getElementById("mealNutritionInfoFatPercent").append(fatTitle);
  };

  toggleConfirm = async () => {
    const getDisplay = S("#mealViewerConfirmBG").getStyle("display");
    if (getDisplay === "none") {
      S("#mealViewerConfirmBG").css({ display: "block" });
      S("#mealViewerConfirmContainer").css({ display: "block" });
      await wait(20);
      S("#mealViewerConfirmBG").css({ opacity: 1 });
      S("#mealViewerConfirmContainer").css({ opacity: 1, bottom: "50%" });
    } else {
      S("#mealViewerConfirmBG").css({ opacity: 0 });
      S("#mealViewerConfirmContainer").css({ opacity: 0 });
      await wait(350);
      S("#mealViewerConfirmBG").removeAttr("style");
      S("#mealViewerConfirmContainer").removeAttr("style");
    }
  };

  checkHash = () => {
    let hash;
    switch (window.location.hash.split("#")[2]) {
      case "breakfast":
        hash = "الفطور";
        break;
      case "lunch":
        hash = "الغداء";
        break;
      case "dinner":
        hash = "العشاء";
        break;
      case "snacks":
        hash = "وجبة خفيفة";
        break;
      default:
        break;
    }
    return hash;
  };

  render() {
    return (
      <Fragment>
        <div id="mealViewerConfirmBG" onClick={this.toggleConfirm}></div>
        <div id="mealViewerConfirmContainer">
          <p>هل تريد حذف هذه الوجبة من المفضلة؟</p>
          <button onClick={this.trashButtonHandle}>نعم</button>
          <button onClick={this.toggleConfirm}>لا</button>
        </div>
        <div id="mealViewerPage">
          <header
            id="mealViewerHeader"
            style={{
              gridTemplateColumns: window.location.pathname.includes("search") ? "repeat(1, 1fr) 0.4fr" : "repeat(2, 1fr) 0.4fr",
            }}
          >
            <svg id="mealViewerBackButton" onClick={this.backButtonHandle} xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24">
              <path d="M20 11H7.83l5.59-5.59L12 4l-8 8 8 8 1.41-1.41L7.83 13H20v-2z" />
            </svg>
            <svg
              id="mealViewerTrashCan"
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 24 24"
              onClick={this.toggleConfirm}
              style={{ display: window.location.pathname.includes("search") ? "none" : "block" }}
            >
              <path d="M3 6v18h18v-18h-18zm5 14c0 .552-.448 1-1 1s-1-.448-1-1v-10c0-.552.448-1 1-1s1 .448 1 1v10zm5 0c0 .552-.448 1-1 1s-1-.448-1-1v-10c0-.552.448-1 1-1s1 .448 1 1v10zm5 0c0 .552-.448 1-1 1s-1-.448-1-1v-10c0-.552.448-1 1-1s1 .448 1 1v10zm4-18v2h-20v-2h5.711c.9 0 1.631-1.099 1.631-2h5.315c0 .901.73 2 1.631 2h5.712z" />
            </svg>
            <svg
              id="mealViewerEdit"
              onClick={this.editHandle}
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 24 24"
              style={{ display: window.location.pathname.includes("search") ? "none" : "block" }}
            >
              <path d="M7.127 22.562l-7.127 1.438 1.438-7.128 5.689 5.69zm1.414-1.414l11.228-11.225-5.69-5.692-11.227 11.227 5.689 5.69zm9.768-21.148l-2.816 2.817 5.691 5.691 2.816-2.819-5.691-5.689z" />
            </svg>
            <p id="mealViewerHeaderTitle" style={{ display: window.location.pathname.includes("search") ? "block" : "none" }}>
              {this.checkHash()}
            </p>
          </header>
          <div id="mealViewerHeaderButtonsContainer">
            <p id="mealViewerHeaderItemName">{this.props.name ? this.props.name : tmpMealName}</p>

            <div id="mealViewerOkButtonBg" onClick={this.okButtonHandle}>
              <svg id="mealViewerOkButton" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24">
                <path d="M21 6.285l-11.16 12.733-6.84-6.018 1.319-1.49 5.341 4.686 9.865-11.196 1.475 1.285z" />
              </svg>
            </div>
            <div id="mealViewerOkButtonAnimation1"></div>
            <div id="mealViewerOkButtonAnimation2"></div>
          </div>

          <p id="mealViewerKcal">
            {Math.round(this.props.kcal || this.props.kcal === 0 ? this.props.kcal : tmpKcal)}
            <span>سعرة حرارية</span>
          </p>
          <div
            id="mealViewerSelectMealContainer"
            style={{ display: window.location.hash === "#Meals#MealViewer" ? "block" : "none" }}
          >
            <select id="mealViewerSelectMeal">
              <option className="itemViewerSelectMealItems" value="breakfast">
                الفطور
              </option>
              <option className="itemViewerSelectMealItems" value="lunch">
                الغداء
              </option>
              <option className="itemViewerSelectMealItems" value="dinner">
                العشاء
              </option>
              <option className="itemViewerSelectMealItems" value="snacks">
                وجبة خفيفة
              </option>
            </select>
          </div>
          <div id="mealNutritionInfoPercentContainer">
            <div id="mealNutritionInfoCrabsPercent"></div>
            <div id="mealNutritionInfoProteinPercent"></div>
            <div id="mealNutritionInfoFatPercent"></div>
          </div>
          <div id="foodsInTheMealContainer">
            <p className="mealViewerInfoTitles">الطعام الموجود في الوجبة</p>
            {this.renderFoodsInTheMeal()}
          </div>
          <p className="mealViewerInfoTitles">المعلومات الغذائية للوجبة</p>
          <div id="mealViewerNutritionInfoDetail">
            <div id="mealViewerNutritionInfoTitles">
              <p className="mealViewerNutritionInfoMain">البروتين</p>
              <p className="mealViewerNutritionInfoMain">الكربوهيدرات</p>
              <p className="mealViewerNutritionInfoSub">الألياف</p>
              <p className="mealViewerNutritionInfoSub">السكر</p>
              <p className="mealViewerNutritionInfoMain">الدهون</p>
              <p className="mealViewerNutritionInfoSub">الدهون المشبعة</p>
              <p className="mealViewerNutritionInfoSub">الدهون الغير مشبعة</p>
              <p className="mealViewerNutritionInfoMain">آخر</p>
              <p className="mealViewerNutritionInfoSub">الكوليسترول</p>
              <p className="mealViewerNutritionInfoSub">الصوديوم</p>
              <p className="mealViewerNutritionInfoSub">البوتاسيوم</p>
            </div>
            <div id="mealViewerNutritionInfoValues">
              <p className="mealViewerNutritionInfoMain">{Number(this.sumNutrition("protein").toFixed(1))} غ</p>
              <p className="mealViewerNutritionInfoMain">{Number(this.sumNutrition("carb").toFixed(1))} غ</p>
              <p className="mealViewerNutritionInfoSub">{Number(this.sumNutrition("fiber").toFixed(1))} غ</p>
              <p className="mealViewerNutritionInfoSub">{Number(this.sumNutrition("sugar").toFixed(1))} غ</p>
              <p className="mealViewerNutritionInfoMain">{Number(this.sumNutrition("fat").toFixed(1))} غ</p>
              <p className="mealViewerNutritionInfoSub">{Number(this.sumNutrition("saturatedFat").toFixed(2))} غ</p>
              <p className="mealViewerNutritionInfoSub">{Number(this.sumNutrition("unsaturatedFat").toFixed(2))} غ</p>
              <p className="mealViewerNutritionInfoMain" style={{ visibility: "hidden" }}>
                ......
              </p>
              <p className="mealViewerNutritionInfoSub">{this.sumNutrition("cholesterol").toFixed(0)} ملغ</p>
              <p className="mealViewerNutritionInfoSub">{this.sumNutrition("sodium").toFixed(0)} ملغ</p>
              <p className="mealViewerNutritionInfoSub">{this.sumNutrition("potassium").toFixed(0)} ملغ</p>
            </div>
          </div>
        </div>
      </Fragment>
    );
  }
}
